import React, { Component } from 'react'
import Slider from 'react-slick'
import './slick/slick.css'

export default class TentimonialsCarousel extends Component {
  render() {
    const settings = {
      dots: true,
      lazyLoad: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      initialSlide: 2,
      autoplay: true,
      autoplaySpeed: 4000,
    };
    return (
      <div>
        <h2>What Members Have to Say</h2>
        <Slider {...settings}>
          <div className="index-section-testimonials__testimonial">
            <p><em>It makes fitness fun &amp; flexible, being a sportsperson and adventure junkie I hated being tied down to a particular gym. With Fitato, it's like a new adventure waiting for you every day, sheer variety in fitness with a single pass.</em></p>
            <img src="static/index/testimonials/vishesh.png" />
            <p className="index-section-testimonials__testimonial--name">Vishesh Parashar</p>
            <p>Aundh, MAERSK</p>
          </div>
          <div className="index-section-testimonials__testimonial">
            <p><em>It makes fitness fun &amp; flexible, being a sportsperson and adventure junkie I hated being tied down to a particular gym. With Fitato, it's like a new adventure waiting for you every day, sheer variety in fitness with a single pass.</em></p>
            <img src="static/index/testimonials/karen.webp" />
            <p className="index-section-testimonials__testimonial--name">Vishesh Parashar</p>
            <p>Aundh, MAERSK</p>
          </div>
          <div className="index-section-testimonials__testimonial">
            <p><em>It makes fitness fun &amp; flexible, being a sportsperson and adventure junkie I hated being tied down to a particular gym. With Fitato, it's like a new adventure waiting for you every day, sheer variety in fitness with a single pass.</em></p>
            <img src="static/index/testimonials/aninda.webp" />
            <p className="index-section-testimonials__testimonial--name">Vishesh Parashar</p>
            <p>Aundh, MAERSK</p>
          </div>
        </Slider>
      </div>
    );
  }
}


