import React from 'react'
import Link from 'gatsby-link'

const FooterInfo = () => (
  <div className="footer-info" style={{backgroundImage: 'url(static/footer-bg.webp)'}}>
    <div className="container">
      <div className="footer-info__col1">
        <h4>Own a Facility? Become a Partner</h4>
        <p>Own a gym? or a fitness studios? Expand your business with Fitato by growing your clientele, increasing revenue and brand exposure. Fitato helps in better utilization of your facility as well as a significant increase in revenue. Become a partner today, drop us an email <a href="mailto:partners@fitato.fit">partners@fitato.fit</a>.</p>
      </div>
      <div className="footer-info__col2">
        <h4>Learn More</h4>
        <ul className="footer-ul">
          <li><a href="http://blog.fitato.fit">Blog</a></li>
          <li><Link to="/faq">FAQ</Link></li>
          <li><Link to="/reviews">Reviews</Link></li>
          <li><Link to="/privacy-policy">Privacy Policy</Link></li>
        </ul>
      </div>
      <div className="footer-info__col3">
        <h4>Contact</h4>
        <ul className="footer-ul">
          <li><a href="mailto:connect@fitato.fit">connect@fitato.fit</a></li>
          <li><a href="tel:+918408999984">+91 8408999984</a></li>
          <li><a href="tel:+918390229669">+91 8390229669</a></li>
        </ul>
        <ul className="footer-social">
          <li>
            <a href="https://www.facebook.com/Fitato.fit/" target="_blank" rel="noopener">
              <img src="static/icons/facebook.png" />
            </a>
          </li>
          <li>
            <a href="https://twitter.com/fitatofit" target="_blank" rel="noopener">
              <img src="static/icons/twitter.png" />
            </a>
          </li>
          <li>
            <a href="https://plus.google.com/+FitatoFitness" target="_blank" rel="noopener">
              <img src="static/icons/googleplus.png" />
            </a>
          </li>
          <li>
            <a href="https://www.youtube.com/watch?v=pKC3-oPRudA" target="_blank" rel="noopener">
              <img src="static/icons/youtube.png" />
            </a>
          </li>
          <li>
            <a href="https://www.instagram.com/fitato_fit/" target="_blank" rel="noopener">
              <img src="static/icons/instagram.png" />
            </a>
          </li>
        </ul>
      </div>
    </div>
  </div>
)

export default FooterInfo



